import { Component, inject, Inject, Input, OnInit, PLATFORM_ID, TemplateRef, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { DestroyService } from '@shared/services/destroy.service';
import { filter, map, takeUntil } from 'rxjs';
import { FilterDropdownValue, RecentSearch } from '@core/models';
import { isPlatformBrowser } from '@angular/common';
import { MatAutocompleteTrigger } from '@angular/material/autocomplete';
import { MatDialog } from '@angular/material/dialog';
import { LocationSearchDialogComponent } from '../location-search-dialog/location-search-dialog.component';
import { FiltersService } from '../../services/filters.service';
import { MatInput } from '@angular/material/input';
import { CommonFacade } from '@core/facades/common.facade';
import { ButtonType } from '@shared/components/button/button.component';
import { PropertyAlertDialogComponent } from '../property-alert-dialog/property-alert-dialog.component';
import { ViewportService } from '@core/services/viewport.service';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthService } from '@core/data-access/services/auth.service';
import { NgbOffcanvas, NgbOffcanvasRef } from '@ng-bootstrap/ng-bootstrap';
import { AisleType, CountyType, FilterAction, FiltersPanelClass, FilterType, SectionType } from '@core/enums';
import { IntercomService } from '@core/services/intercom.service';
import { APP_ROUTE } from '@core/constants';
import * as countiesJson from '@shared/json/counties/counties.json';
import { RouteService } from '@core/services/route.service';

// TODO: Just use one componet for Location Search. We are using different components for both desktop and mobile.
@Component({
    selector: 'findqo-location-search',
    templateUrl: './location-search.component.html',
    styleUrl: './location-search.component.scss',
    providers: [DestroyService],
    standalone: false
})
export class LocationSearchComponent implements OnInit {
	constructor(
		private readonly destroy: DestroyService,
		private readonly dialog: MatDialog,
		@Inject(PLATFORM_ID) public platformId,
		private readonly filtersService: FiltersService,
		private readonly commonFacade: CommonFacade,
		public viewport: ViewportService,
		private route: ActivatedRoute,
		public authService: AuthService,
		private readonly ngbOffCanvas: NgbOffcanvas,
		private router: Router,
		private intercomService: IntercomService,
		private routeService: RouteService
	) {}

	public readonly offcanvas = inject(NgbOffcanvas);
	public readonly counties = countiesJson.data.all;

	@ViewChild('search') search: MatInput;

	@Input() buttonCss = '';
	@Input() mobileContainerClass = '';
	@Input() containerClass = '';
	@Input() isFilterDialog = false;

	// isRent = true;
	// isBuy = false;
	// btnSubmitLabel = 'Done';
	// Labelname = 'Rent';

	propertyFor: 'RENT' | 'BUY' = 'RENT';

	get selectedLocations() {
		return this.filtersService.locations;
	}

	locations: FilterDropdownValue[];
	locationControl = new FormControl('');
	isSearchBarOpened = false;
	recentSearch: RecentSearch[];
	readonly buttonType = ButtonType;

	updateLocation(event) {
		this.locationControl.reset();
		const LOCATION_VALUE = event?.option?.value;

		if (!this.selectedLocations?.value.find((sl) => sl === LOCATION_VALUE)) {
			const LOCATION = this.filtersService.locations.controls.find((l) => l.value === LOCATION_VALUE);

			if (!LOCATION) {
				this.filtersService.locations.push(new FormControl(LOCATION_VALUE));
			}

			this.filtersService.updateRoute();
		}
	}

	handleInput(event): void {
		this.locationControl.patchValue(event.target.value);
	}

	handlePanel(event, trigger: MatAutocompleteTrigger): void {
		event.stopPropagation();
		this.isSearchBarOpened = true;

		setTimeout(() => {
			if (this.search) {
				this.search.value = '';
				trigger.openPanel();
				this.search?.focus();
			}
		}, 0);
	}

	handleClearSelection($event): void {
		$event.stopImmediatePropagation();
		this.filtersService.locations.clear();
		this.filtersService.updateRoute();
	}

	openDialog(): void {
		this.dialog
			.open(LocationSearchDialogComponent, {
				width: '100%',
				height: '100%',
				maxWidth: '500px',
				panelClass: ['overflow-hidden'],
				data: {
					recentSearch: this.recentSearch,
					selected: this.selectedLocations?.value,
				},
			})
			.afterClosed()
			.subscribe((res) => {
				if (res) {
					res.forEach((element) => {
						this.updateLocation({ option: { value: element } });
					});

					this.filtersService.updateRoute();
				}
			});
	}

	remove(loc) {
		const INDEX = this.filtersService.locations.controls.findIndex((l) => l.value === loc);
		this.filtersService.locations.removeAt(INDEX);
		this.filtersService.updateRoute();
	}

	private fetchLocations(): void {
		this.locationControl.valueChanges
			.pipe(
				filter(Boolean),
				map((searchText: string) => this.counties.filter((county) => county.name.toLocaleLowerCase().includes(searchText.trim().toLocaleLowerCase()))),
				takeUntil(this.destroy)
			)
			.subscribe((locations) => (this.locations = locations));
	}

	onShowAlertsDialog() {
		if (!this.authService.user()) {
			this.authService.openAuthDialog();
			return;
		}

		this.dialog.open(PropertyAlertDialogComponent, {
			disableClose: true,
			panelClass: 'property-alert-dialog',
			data: {
				section: this.route.snapshot.params['section'],
				aisle: this.route.snapshot.params['aisle'],
				location: { ...(this.filtersService.locations.value.length ? { countyReference: this.filtersService.locations.value[0] } : {}) },
			},
		});
	}

	onOpenFiltersforRent(content: TemplateRef<NgbOffcanvasRef>) {
		const offCanvasRef = this.ngbOffCanvas.open(content, { position: 'bottom', panelClass: FiltersPanelClass.BASE_MIN_MAX_FILTER });

		offCanvasRef.dismissed.subscribe((action: FilterAction) => {
			switch (action) {
				case FilterAction.UPDATE:
					this.filtersService.updateRoute();
			}
		});
	}

	onResetFilters(): void {
		this.propertyFor = 'RENT';
		this.onSubmit();
	}

	onPropertyclick(value: string) {
		if (value == 'Rent') {
			this.propertyFor = 'RENT';
		} else {
			this.propertyFor = 'BUY';
		}
	}

	onClose(): void {
		this.offcanvas.dismiss();

		if (this.router.url === '/') {
			this.intercomService.toggelIntercomPopup(false);
		}
	}
	currentLocation(): string {
		const CURRENT_ROUTE = this.router.routerState.root.firstChild;
		return CURRENT_ROUTE.snapshot.params?.[FilterType.LOCATION_REFERENCE] || CURRENT_ROUTE.snapshot.data?.[FilterType.LOCATION_REFERENCE] || CountyType.DEFAULT;
	}

	onSubmit(): void {
		this.ngbOffCanvas.dismiss(FilterAction.UPDATE);
		if (this.propertyFor == 'RENT') {
			this.router.navigate([APP_ROUTE.rentAdsList]);
			this.router.navigate([SectionType.PROPERTIES_FOR_RENT, CountyType.DEFAULT, AisleType.RENT_RESIDENTIAL]);
		} else {
			this.router.navigate([APP_ROUTE.sellresidential]);
			this.router.navigate([SectionType.PROPERTIES_FOR_SALE, CountyType.DEFAULT, AisleType.SALE_RESIDENTIAL]);
		}
	}

	ngOnInit(): void {
		if (isPlatformBrowser(this.platformId)) {
			this.fetchLocations();
			this.commonFacade.recentSearches$.subscribe((res) => (this.recentSearch = res));

			this.route.params.subscribe((res) => {
				if (res['section'] == 'properties-for-sale') {
					this.onPropertyclick('Buy');
				} else {
					this.onPropertyclick('Rent');
				}
			});
		}
	}
}
