import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { FilterDropdownValue, RecentSearch } from '@core/models';
import { FormControl } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { filter, map, takeUntil } from 'rxjs';
import { MatAutocompleteTrigger } from '@angular/material/autocomplete';
import { CommonFacade } from '@core/facades/common.facade';
import * as countiesJson from '@shared/json/counties/counties.json';
import { DestroyService } from '@shared/services/destroy.service';

// TODO: Just use one componet for Location Search. We are using different components for both desktop and mobile.
@Component({
    selector: 'findqo-location-search-dialog',
    templateUrl: './location-search-dialog.component.html',
    styleUrl: './location-search-dialog.component.scss',
    providers: [DestroyService],
    standalone: false
})
export class LocationSearchDialogComponent implements OnInit {
	constructor(
		@Inject(MAT_DIALOG_DATA) public data,
		private readonly commonFacade: CommonFacade,
		protected readonly dialogRef: MatDialogRef<LocationSearchDialogComponent>,
		private readonly destroy: DestroyService
	) {
		this.selectedLocations = data?.selected ?? [];
	}

	recentSearch: RecentSearch[];
	selectedLocations = [];

	@ViewChild(MatAutocompleteTrigger) autoTrigger: MatAutocompleteTrigger;

	locations: FilterDropdownValue[];
	locationControl = new FormControl('');

	public readonly counties = countiesJson.data.all;

	updateLocation(value) {
		this.locationControl.reset();
		this.autoTrigger?.openPanel();

		if (!this.selectedLocations?.find((sl) => sl === value)) {
			this.selectedLocations.push(value);
		}
	}

	handleInput(event): void {
		this.locationControl.patchValue(event.target.value);
	}

	remove(loc) {
		const INDEX = this.selectedLocations.findIndex((lc) => lc === loc);

		this.selectedLocations.splice(INDEX, 1);
	}

	private fetchLocations(): void {
		this.locationControl.valueChanges
			.pipe(
				filter(Boolean),
				map((searchText: string) => this.counties.filter((county) => county.name.toLocaleLowerCase().includes(searchText.trim().toLocaleLowerCase()))),
				takeUntil(this.destroy)
			)
			.subscribe((locations) => (this.locations = locations));
	}

	submit(): void {
		this.dialogRef.close(this.selectedLocations);
	}

	ngOnInit(): void {
		this.commonFacade.recentSearches$.subscribe((res) => {
			this.recentSearch = res;
		});

		this.fetchLocations();
	}
}
